<template>
	<view class="express_box">
		<view class="echartsTop" :style="isUnit?'justify-content:flex-end':''" v-if="!unitSelect">
			<text v-if="!isUnit">单位：{{unitText}}</text>
			<view class="barSelect">
				<text :class="{active:searchVal===0}" @click="change(0)">年</text>
				<text :class="{active:searchVal===1}" @click="change(1)">季</text>
				<text :class="{active:searchVal===2}" @click="change(2)">月</text>
			</view>
			<!-- <view class="dropWrap">
				<u-dropdown>
					<u-dropdown-item v-model="searchVal" :title="searchlist[searchVal].label" :options="searchlist" @change="change"></u-dropdown-item>
				</u-dropdown>
			</view> -->
		</view>
		<!-- 柱状图 -->
		<view class="echarts_wrap">
			<uni-icons class="icon arrow left" type="arrowleft" size="24" :color="timeSelect.prev?'#03958C':'#DBE4F9'" @click="prevTap"></uni-icons>
			<view class="barEcharts"><barEcharts :Column="chartData"></barEcharts></view>
			<uni-icons class="icon arrow right" type="arrowright" size="24" :color="timeSelect.next?'#03958C':'#DBE4F9'" @click="nextTap"></uni-icons>
		</view>
	</view>
</template>

<script>
	import barEcharts from '@/components/echartsComponent/barEcharts.vue';
	export default {
		props:['chartData', 'isUnit', 'unitText', 'timeUnit', 'timeSelect', 'unitSelect'],
		data() {
			return {
				searchVal: 2,
				searchlist: [{
						value: '0',
						label: '年'
					},
					{
						value: '1',
						label: '季'
					}, {
						value: '2',
						label: '月'
					}
				],
				barChartData:{
					categories: [],
					series: [{
						data: []
					}],
				}
			}
		},
		created(){
			this.searchVal = this.timeUnit
		},
		methods:{
			// 左翻
			prevTap(){
				if(this.timeSelect.prev){
					let that = this
					setTimeout(function(){
						that.$emit('prevTap')
					},500)
				}
			},
			// 右翻
			nextTap(){
				if(this.timeSelect.next){
					let that = this
					setTimeout(function(){
						that.$emit('nextTap')
					},500)
				}
			},
			// 选择筛选条件
			change(index){
				this.$emit('unitTap',index)
			}
		},
		components:{
			barEcharts
		},
		watch:{
			timeUnit(val) {
			  this.searchVal = val
			}
		}
	}
</script>

<style lang="scss">
	.dropWrap{
		z-index: 1!important;
	}
	.barSelect{
		height: 40rpx;
		border: 2rpx solid #E6E6E6;
		border-radius: 8rpx;
		display: flex;
		align-items: center;
		text{
			width: 60rpx;
			height: 40rpx;
			line-height: 40rpx;
			border-radius: 8rpx;
			display: inline-block;
			text-align: center;
		}
		.active{
			background-color: #03958C;
			color: #fff;
		}
	}
</style>
